.home {
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.headerContainer {
  width: auto;
  margin-left: 30px;
}
.headerContainer h1 {
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 70px;
  color: rgb(15, 168, 229);
}
.headerContainer p {
  margin-top: 15px;
  padding: 0;
  font-size: 40px;
  color: white;
  font-weight: lighter;
}
.headerContainer button {
  background-color: black;
  margin-top: 20px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  height: 50px;
  width: 180px;
  text-decoration: none;
  font-size: 20px;
  color: rgb(53, 207, 234);
}
@media only screen and (max-width: 600px) {
  .home {
    justify-content: center;
    align-items: center;
  }
  .headerContainer {
    margin-left: 0;
    border-radius: 10px;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.574);
    flex-direction: column;
  }
  .headerContainer h1 {
    font-weight: bold;
    font-size: 40px;
    height: 30px;
    margin: 15px 0px;
    color: rgb(15, 168, 229);
  }
  .headerContainer p {
    margin-top: 10px;
    font-size: 25px;
  }
  .headerContainer button {
    background-color: white;
    margin-top: 20px;
  }
}
